
<template>
	<el-row class="forgotUsernameContainer">
	<!--header-->
		<el-col :span="24" class="header">
      <el-row>
        <el-col :span="2" :offset="2"  v-if="Xs == true">
            <el-row class="headerLogo"  type="flex" align="middle">
              <img src="../../static/img/ethereumWebImg/i13.png" class="headerLogoIn" v-if="Xs == true" v-on:click="indexLink">
            </el-row>
          </el-col>

          <el-col :span="10" :offset="0" :sm="{span: 4, offset: 4}" :md="{span: 4, offset: 4}" v-if="Xs == false">
            <el-row class="headerLogo"  type="flex" align="middle">
              <img src="../../static/img/ethereumWebImg/logo_01.png" class="headerLogoIn"><!--********d-none d-sm-block*******-->
            </el-row>
          </el-col>
          
          <el-col :span="8" :offset="10" :sm="{span: 10, offset: 2}" :md="{span: 10, offset: 2}"  class="headerTextOut" v-if="LoginVerify==false">
            <el-row class="headerText" type="flex" align="middle">
              <el-col :span="24" :offset="0" :sm="{span: 4, offset: 18}" class="headerLogoInButton" >
              <!-- <el-col :span="24" class="headerLogoInButton" v-if="Xs != false"> -->
                <el-row class="languageMenu">
                    <el-dropdown trigger="click">
                      <el-row class="el-dropdown-link headerLogoInLanguageOut"  type="flex" align="middle">
                       <img src="../../static/img/ethereumWebImg/i01.png" class="headerLogoInLanguage" />&nbsp;&nbsp; 繁體中文
                      </el-row>
                      <el-dropdown-menu slot="dropdown" class="languageDropdownMenu"  v-on:click="openLanguageDropdownMenu">
                        <el-dropdown-item>English</el-dropdown-item>
                        <el-dropdown-item>中文(繁體)</el-dropdown-item>
                        <el-dropdown-item>中文(簡體)</el-dropdown-item>
                      </el-dropdown-menu>
                    </el-dropdown>
                </el-row>
              <!-- </el-col> -->
              </el-col>
            </el-row>
          </el-col>

      </el-row>
 <!--********Header*******-->     
 <el-row>
                      <transition name="modal">
                        <div class="modal-mask-errorModal" v-show="showRegisterErrorModal">
                          <div class="modal-wrapper">
                              <el-col :span="22" :offset="1" :sm="{span: 6, offset: 9}">
                                <el-row class="modal-container">
                                  <el-col :span="24">
                                     <el-row type="flex"  align="middle" justify="end">
                                    <i class="el-icon-close" v-on:click="showRegisterErrorModal=false"></i>
                                     </el-row>
                                  </el-col>
                                  <el-col :span="24" class="modal-container-body">
                                    <slot>
                                      <el-col :span="24" class="modal-container-body-title">{{errorTitle}}</el-col>
                                      <el-col :span="24" class="modal-container-body-text">{{errorMessage}}</el-col>
                                    </slot>
                                  </el-col>
                                </el-row>
                              </el-col>
                          </div>
                        </div>
                      </transition>
                    </el-row>   	
                    <el-row>
                      <transition name="modal">
                        <div class="modal-mask-errorModal" v-show="showRegisterSuccessModal">
                          <div class="modal-wrapper">
                              <el-col :span="22" :offset="1" :sm="{span: 6, offset: 9}">
                                <el-row class="modal-container">
                                  <el-col :span="24">
                                     <el-row type="flex"  align="middle" justify="end">
                                    <i class="el-icon-close" v-on:click="showRegisterSuccessModal=false"></i>
                                     </el-row>
                                  </el-col>
                                  <el-col :span="24" class="modal-container-body">
                                    <slot>
                                      <el-col :span="24">請至信箱取得帳號</el-col>
                                    </slot>
                                  </el-col>
                                </el-row>
                              </el-col>
                          </div>
                        </div>
                      </transition>
                    </el-row>  	
      </el-col>
    <!--header-->
	
	<!--main-->
    <el-col :span="24" class="main">
      <el-col id="main"  :span="24">
        <el-main class="content-container">
          <el-col :span="24" class="content-wrapper">
            <el-col :span="24" :offset="0" :sm="{span: 12, offset: 8}">
              <el-row>
                            <el-col :span="16" :offset="4" :sm="{span: 16, offset: 0}" class="registerTitle">
                                忘記帳號
                            </el-col>
                            <el-col :span="20" :offset="2" :sm="{span: 16, offset: 0}" class="registerInputOut">
                                <input class="registerInputIn" placeholder="信箱" v-model="email">
                            </el-col>
                            <el-col :span="20" :offset="2" :sm="{span: 16, offset: 0}" class="registerButtonOut">
                                <el-row type="flex" align="middle" justify="center" class="registerButtonIn" v-on:click.native="forgotUsername">
                                    <div class="registerButtonText">送出</div>
                                </el-row>
                            </el-col>
                </el-row>
              </el-col>
          </el-col>
        </el-main>
      </el-col>


    </el-col>
    <!--main-->

	</el-row>
</template>

<script>
import Client from "../js/Client.js";
import GameMgr from "../js/GameMgr.js"; //
import WebSocketNameTag from "../js/WebSocketNameTag.js";
import LoginLayer from "../js/Login.js";

export default {
  data() {
    if (document.body.clientWidth < 768) {
      var Xs = true;
      GameMgr.getInstance().setXs(Xs);
    } else {
      var Xs = false;
      GameMgr.getInstance().setXs(Xs);
    }
    var LoginVerify = false; //false//true
    GameMgr.getInstance().setLoginVerify(LoginVerify);

    return {
      LoginVerify: LoginVerify,
      languageDropdownMenuTop: "top:80px;",
      showMenuName: "home0",
      showMenuXs: false,
      Xs: Xs,
      connectionAliveInterval: "",
      email: "",
      errorTitle:"",
      errorMessage:"",

      showRegisterSuccessModal:false,
      showRegisterErrorModal:false,

      //***Head***//
      options: [
        /*{
          id: "tw",
          label: "繁體中文"
        },*/
        {
          id: "cn",
          label: "简体中文"
        },
        {
          id: "en",
          label: "English"
        }
      ],
      value1: "简体中文",
      //***Head***//

      //**Language**//
      lang: this.$lang.cn
      //**Language**//
    };
  },

  watch: {
    value1: {
      handler: function() {
        console.log(
          "== value1!!!!!!!!!!!!!!!!! ==" +
            this.value1.id +
            ", Lang:" +
            this.lang
        );
        if (this.value1.id == "en") {
          this.lang = this.$lang.en;
          GameMgr.globalLang = this.value1.id;
          console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
          window.myApp.$emit("onmodifyLang");
        } else if (this.value1.id == "cn") {
          this.lang = this.$lang.cn;
          GameMgr.globalLang = this.value1.id;
          console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
          window.myApp.$emit("onmodifyLang");
        } else if (this.value1.id == "tw") {
          this.lang = this.$lang.tw;
          GameMgr.globalLang = this.value1.id;
          console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
          window.myApp.$emit("onmodifyLang");
        }
      }
    }
  },
  //**Header**//

  methods: {
    forgotUsername: function() {
        var loginlayer = new LoginLayer();
        loginlayer.forgotUsername(
          this.email
        );
    },
    openLanguageDropdownMenu: function() {},
    menuClick: function(menuName) {
      console.log(menuName); //sdebugger
      if (this.betRunningXs == 24) {
        this.betRunningXs = 0;
        this.mainXs = 24;
      }
      this.showMenuName = menuName;
    },
    indexLink: function() {
      this.$router.push({ path: "/" });
    },
    OpenMenuXs: function() {
      if (this.showMenuXs == false) {
        //debugger
        this.showMenuXs = true;
      } else {
        this.showMenuXs = false;
      }
    },

    getLanguageData: function() {
      //**Language**//
      var Lang = GameMgr.globalLang;
      if (Lang == "en") {
        this.value1 = "English";
        this.lang = this.$lang.en;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (Lang == "cn") {
        this.value1 = "簡體中文";
        this.lang = this.$lang.cn;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (Lang == "tw") {
        this.value1 = "繁體中文";
        this.lang = this.$lang.tw;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      }
      //**Language**//
    } //,

    // SignOut: function() {
    //   var destoryAll = function() {
    //     window.myApp.$emit("destoryUpdataPrice");
    //   };
    //   clearInterval(this.connectionAliveInterval);
    //   destoryAll();
    //   // window.myApp.$on;debugger
    //   window.myApp.$off();
    //   this.$router.push({ path: "/login" });
    // }
  },
  beforeCreate: function() {
    console.log("== HomeBeforeCreate ==");
    if (document.body.clientWidth < 768) {
      var Xs = true;
      GameMgr.getInstance().setXs(Xs);
    } else {
      var Xs = false;
      GameMgr.getInstance().setXs(Xs);
    }

    // var HomeVerify = GameMgr.getInstance().getHomeVerify();

    // if (HomeVerify == "Lobby") {
    //   GameMgr.getInstance().setHomeVerify("Home");
    //   console.log("LobbyLobbyLobbyLobbyLobby");
    // } else {
    //   var refresh = function() {
    //     console.log("HomeHomeHomeHomeHome");
    //     this.$router.push({ path: "/login" });
    //   }.bind(this);
    //   refresh();
    // }
  },
  created: function() {
    console.log("== HomeCreated ==");
    window.myApp.$on(
      "onRegisterError",
      function() {
      var errorTitle = GameMgr.getInstance().getErrorTitle();
      var errorMessage = GameMgr.getInstance().getErrorMessage();
      this.errorTitle=errorTitle;
      this.errorMessage=errorMessage;
      this.showRegisterErrorModal=true;
      }.bind(this)
    );
    window.myApp.$on(
      "onRegisterSuccess",
      function() {
      this.showRegisterSuccessModal=true;
      }.bind(this)
    );
    // this.OpenMenuXs();
  },
  mounted: function() {
    console.log("== mounted ==");
  },
  destroyed: function() {
    //window.myApp.$off();
    console.log("== HomeDestroyed ==");
    // this.lobbyLayer.destory();
    // var destoryUpdataPrice = function() {
    //   window.myApp.$emit("destoryUpdataPrice");
    // };
    clearInterval(this.connectionAliveInterval);
    // destoryUpdataPrice();
    // window.myApp.$off("onServerClose");
    window.myApp.$off("onRegisterError");
    window.myApp.$off("onRegisterSuccess");
  }
};
</script>

<style scoped lang="scss">
@import "~scss_vars";
.forgotUsernameContainer {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  .el-col {
    padding: 0px 0px;
  }
  .el-row {
    padding: 0px 0px;
  }
  //***********************
  .headerLogo {
    text-align: left;
    height: 80px;
    /* padding-top:10px; */
  }

  .headerLogoIn {
    // padding-top: 10px;
    max-width: 100%;
  }

  .headerLogoInLanguage {
    max-width: 100%;
  }

  .headerTextOut {
    height: 100%;
  }
  .headerText {
    // padding-top: 10px;
    // top: 8px;
    color: #9d8d6b;
    /* font-weight: bold; */
    text-align: center;
    height: 80px;
    font-size: 15px;
  }
  .headerLoginTextA {
    text-align: center;
    color: #ffffff;
  }
  //**********************************************************************
  .header {
    //0510
    height: 80px;
    // line-height: 60px;
    // background: $color-primary;
    background-color: #3c3c3c;
    color: #fff;
    text-align: center;
    //Modal//
  }
  .main {
    //0510
    z-index: 2;
    display: flex;
    // background: #324057;
    position: absolute;
    top: 80px; //因 header 的 height: 40px
    bottom: 0px;
    overflow: hidden;
    //overflow-y: hidden;/////
    .content-container {
      background: #1e1e1e;
      flex: 1;
      height: 100%;
      overflow-y: scroll;
      padding: 0px;
      overflow: auto; //***
      .content-wrapper {
        height: 100%;
        background-color: #1e1e1e;
        box-sizing: border-box;
        .registerTitle {
          padding-top: 30px;
          text-align: center;
          color: #ffffff;
          font-size: 30px;
        }
        .registerInputOut {
          margin-top: 30px;
          .registerInputIn {
            // padding-left: 20px;
            text-align: left;
            width: 100%;
            /* height: 100%; */
            height: 50px;
            background-color: #ffffff;
            border-width: 0px;
            // font-size: 20px;
          }
          .registerInputIn::placeholder {
            padding-left: 20px;
          }
          .el-icon-circle-plus-outline {
            color: #ffffff;
            line-height: 50px;
            font-size: 25px;
            cursor: pointer;
          }
        }

        .registerButtonOut {
          cursor: pointer;
          background-color: #00aada;
          border-radius: 0px;
          margin-top: 30px;
          .registerButtonIn {
            height: 50px;
            color: #ffffff;
            .registerButtonText {
              /*0507*/
              /* height: 30px; */
              padding: 0px;
              font-weight: normal;
              font-size: 20px;
              /*color: #ffffff; */
            }
          }
        }
      }
    }
  }
  .mainSm {
    top: 80px;
  }
  .mainXs {
    top: 80px;
  }

  /*****/
  .modal-mask-errorModal {
      position: fixed;
      z-index: 9999;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.45);
      display: table;
      transition: opacity 0.3s ease;

      .modal-wrapper {
        display: table-cell;
        vertical-align: middle;
        .modal-container {
          // width: 500px; //***
          margin: 0px auto;
          // padding: 18px 0px; //top/right
          background-color: rgba(0, 0, 0, 0);
          
          transition: all 0.3s ease;
          font-family: Helvetica, Arial, sans-serif;

          .el-icon-close{
            cursor: pointer;
            padding:10px 0px;
            color:#ffffff;
            font-size:20px;
            font-weight: bolder;
          }
          .modal-container-body {
            border-radius: 2px;
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
            text-align: center;
            background-color: #ffffff;
            // margin: 20px 0;
            padding: 30px 5px;
            color: #383838;
            .modal-container-body-title{
              padding-bottom:20px; 
              font-size:20px;
            }
            .modal-container-body-text{
              font-size:18px;
            }
          }
        }
      }
    }
    .modal-enter {
      opacity: 0;
    }

    .modal-leave-active {
      opacity: 0;
    }

    .modal-enter .modal-container,
    .modal-leave-active .modal-container {
      -webkit-transform: scale(1.1);
      transform: scale(1.1);
    }
    /****/
}
/***/
.languageMenu {
  margin-top: 5px;
  height: 30px;
  .el-dropdown {
    height: 100%;
    .headerLogoInLanguageOut {
      height: 100%;
      color: #9d8d6b;
    }
    .headerLogoInLanguageOut:focus {
      outline-width: 0px;
    }
  }
}
.el-dropdown-menu,
.el-popper[x-placement^="bottom"],
.languageDropdownMenuXs {
  left: 20px;
  margin-top: 20px;
  background-color: #ffffff;
  border-radius: 0px;
  border-width: 0px;
  text-align: center;
  .el-dropdown-menu__item:hover {
    background: #ffffff;
    color: #000000;
  }
}
.languageDropdownMenu {
  left: 20px;
  margin-top: 20px;
  background-color: #ffffff;
  border-radius: 0px;
  border-width: 0px;
  text-align: center;
  .el-dropdown-menu__item:hover {
    background: #ffffff;
    color: #000000;
  }
}

/***/
</style>


