
import Home from './views/Home.vue'
import index from './views/index.vue'
import register from './views/register.vue'
import forgotPassword from './views/forgotPassword.vue'
import forgotUsername from './views/forgotUsername.vue'
import transaction from './views/transaction.vue'
import transactionA from './views/transactionA.vue'
import transaction3 from './views/transaction3.vue'

let routes = [
    {
        path: '/register',
        component: register,
        name: '註冊',
        hidden: false
    },
    {
        path: '/forgotPassword',
        component: forgotPassword,
        name: '忘記密碼',
        hidden: false
    },
    {
        path: '/forgotUsername',
        component: forgotUsername,
        name: '忘記帳號',
        hidden: false
    },
    {
        path: '/',
        component: Home,
        name: 'home0',
        iconCls: 'el-icon-goods',
        leaf: true,
        children: [
            { path: '/', component: index }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'home2',
        iconCls: 'el-icon-goods',
        leaf: true,
        children: [
            { path: '/deposit', component: transaction }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'home1',
        iconCls: 'el-icon-goods',
        leaf: true,
        children: [
            { path: '/withdraw', component: transactionA }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'home3',
        iconCls: 'el-icon-goods',
        leaf: true,
        children: [
            { path: '/transactionHistory', component: transaction3 }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/' }//404
    }

];

export default routes;


