<template>
<div class="index3Page">
  <div class="chart-container">
     <el-row type="flex"  align="middle" justify="space-between">
    <el-col :span="24" class="mainLogo">
     
                        <el-col :span="24" :offset="0" :sm="{span: 12, offset: 6}" class="mainLogoTransactionText" v-if="Xs == false">
                          <!-- <el-row type="flex"  align="middle" justify="center"> -->
                            <span>時間</span>
                            <el-date-picker v-model="startDate" type="date" :clearable = false placeholder="選擇日期" :picker-options="startDateOptions" class="timeSelect">
                            </el-date-picker>
                            <span> 至</span>
                            <el-date-picker v-model="endDate" type="date" :clearable = false  placeholder="選擇日期" :picker-options="endDateOptions" class="timeSelect">
                            </el-date-picker>
                            <select class="timeSelect timeSelectMarginLeft" v-model="searchCategorySelected">
                                <option v-for="item in searchCategoryOptions" :key="item.id" :label="item.label" v-bind:value="item.id"></option>
                            </select>
                            <div class="searchButton" v-on:click="queryTransaction">
                                <img src="../../static/img/ethereumWebImg/i10.png">
                            </div>
                          <!-- </el-row> -->
                        </el-col>
                        <el-col :span="22" :offset="1" class="mainLogoTransactionText" v-if="Xs == true">
                          <el-row type="flex"  align="middle" justify="space-between">
                                <el-date-picker v-model="startDate" type="date" :clearable = false placeholder="選擇日期" :picker-options="startDateOptions" class="timeSelect">
                                </el-date-picker>
                                <span> 至</span>
                                <el-date-picker v-model="endDate" type="date" :clearable = false  placeholder="選擇日期" :picker-options="endDateOptions" class="timeSelect">
                                </el-date-picker>
                            </el-row>
                            </el-col>
                            <el-col :span="22" :offset="1" class="mainLogoTransactionText" v-if="Xs == true">
                              <el-row type="flex"  align="middle" justify="space-between">
                                    <select class="timeSelectXsBottom timeSelectMarginLeft" v-model="searchCategorySelected">
                                        <option v-for="item in searchCategoryOptions" :key="item.id" :label="item.label" v-bind:value="item.id"></option>
                                    </select>
                                    <!-- <el-row type="flex"  align="middle"> -->
                                    <div class="searchButtonXs" v-on:click="queryTransaction">
                                        <img src="../../static/img/ethereumWebImg/i10.png">
                                    </div>
                                    </el-row>
                            </el-col>
                               
        </el-col>
        
    </el-row>
    <el-row> 
                    <el-col :span="24" :offset="0" :sm="{span: 20, offset: 2}" class="mainTransaction" v-on:click="openShowQueryTransactionModal">
                        <el-col :span="24" :offset="0" :sm="{span: 20, offset: 2}">
                           <el-row v-if="Xs == false">
                                    <el-col :span="24" class="mainDialogTransactionStep">
                                        <el-row>
                                            <el-col :span="12">
                                                <el-row>
                                                    <el-col :span="7">日期/時間</el-col>
                                                    <el-col :span="17">錢包位置</el-col>
                                                </el-row>
                                            </el-col>
                                            <el-col :span="12">
                                                <el-row>
                                                    <el-col :span="4">出/入金</el-col>
                                                    <el-col :span="4">兌換比例</el-col>
                                                    <el-col :span="6">虛擬幣/點數</el-col>
                                                    <el-col :span="6">虛擬幣/點數</el-col>
                                                    <el-col :span="4">兌換</el-col>
                                                </el-row>
                                            </el-col>
                                        </el-row>
                                    </el-col>
                                </el-row>
                        </el-col>
                        <el-row>
                            <el-col :span="24" :offset="0" :sm="{span: 20, offset: 2}" class="mainDialogTransaction"  v-bind:style="{ height: boxHeight + 'px' }">
                               
                                <el-row>

                                    <el-col :span="24" class="mainDialogTransactionStep2" v-for="(queryTransaction,index) in queryTransactionList" :key="queryTransaction.order_id" v-bind:class="{'mainDialogTransactionStep2BG1':index%2==1,'mainDialogTransactionStep2BG2':index%2==0}">
                                    
                                        <!-- <el-row type="flex"  align="middle"> -->
                                            <el-col :span="24" :offset="0" :sm="{span: 12, offset: 0}" class="mainDialogTransactionStep2Top" v-if="Xs == false">
                                                <!-- <el-row type="flex"  align="middle"> -->
                                                    <el-col :span="12" :offset="0" :sm="{span: 7, offset: 0}">{{queryTransaction.date}}</el-col>
                                                    <el-col :span="12" :offset="0" :sm="{span: 17, offset: 0}">{{queryTransaction.from_wallet}}</el-col>
                                                <!-- </el-row> -->
                                            </el-col>
                                            <el-col :span="24" :offset="0" :sm="{span: 12, offset: 0}" v-if="Xs == false">
                                                <!-- <el-row type="flex"  align="middle"> -->
                                                    <el-col :span="4">{{queryTransaction.action_type}}</el-col>
                                                    <el-col :span="4">1:{{queryTransaction.exchange_rate}}</el-col>
                                                    <el-col :span="6">以太幣 {{queryTransaction.exchange_value}}</el-col>
                                                    <el-col :span="6">點數 {{(queryTransaction.exchange_value)*(queryTransaction.exchange_rate)}}</el-col>
                                                    <el-col :span="4">{{queryTransaction.status}}</el-col>
                                                <!-- </el-row> -->
                                            </el-col>
                                            <el-col :span="24" :offset="0"  class="mainDialogTransactionStep2Top" v-if="Xs == true">
                                                <el-row type="flex"  align="middle">
                                                    <el-col :span="12" :offset="0">{{queryTransaction.date}}</el-col>
                                                    <el-col :span="4" :offset="0">{{queryTransaction.action_type}}</el-col>
                                                    <el-col :span="4" :offset="0">{{queryTransaction.status}}</el-col>
                                                    <el-col :span="4" :offset="0">
                                                        <img src="../../static/img/ethereumWebImg/i11.png" class="queryTransactionImg" v-on:click="openShowQueryTransactionModal(queryTransaction)">
                                                    </el-col>
                                                </el-row>
                                            </el-col>
                                        <!-- </el-row> -->
                                    </el-col>

                                </el-row>
                            </el-col>
                        </el-row>
                    </el-col>
   </el-row>
                    <el-row>
                      <transition name="modal">
                        <div class="modal-mask-queryTransactionModal" v-show="showQueryTransactionModal">
                          <div class="modal-wrapper">
                              <el-col :span="22" :offset="1" :sm="{span: 6, offset: 9}">
                                <el-row class="modal-container">
                                  <el-col :span="24">
                                     <el-row type="flex"  align="middle" justify="end">
                                    <i class="el-icon-close" v-on:click="showQueryTransactionModal=false"></i>
                                     </el-row>
                                  </el-col>
                                  <el-col :span="24" class="modal-container-body">
                                    <slot>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="8" class="queryTransactionModalDataBoxTitle">時間日期：</el-col>
                                        <el-col :span="16" class="queryTransactionModalDataBoxData">{{queryTransactionXs.date}}</el-col>
                                      </el-row>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="8" class="queryTransactionModalDataBoxTitle">訂單編號：</el-col>
                                        <el-col :span="16" class="queryTransactionModalDataBoxData">{{queryTransactionXs.order_id}}</el-col>
                                      </el-row>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="24" class="queryTransactionModalDataBoxTitle">錢包位置：</el-col>
                                        <el-col :span="22" :offset="1" class="queryTransactionModalDataBoxDataWallet">{{queryTransactionXs.from_wallet}}</el-col>
                                      </el-row>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="10" class="queryTransactionModalDataBoxTitle">出/入金：</el-col>
                                        <el-col :span="14" class="queryTransactionModalDataBoxData">{{queryTransactionXs.action_type}}</el-col>
                                      </el-row>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="10" class="queryTransactionModalDataBoxTitle">兌換比：</el-col>
                                        <el-col :span="14" class="queryTransactionModalDataBoxData">1:{{queryTransactionXs.exchange_rate}}</el-col>
                                      </el-row>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="10" class="queryTransactionModalDataBoxTitle">虛擬幣/點數：</el-col>
                                        <el-col :span="14" class="queryTransactionModalDataBoxData">以太幣 {{queryTransactionXs.exchange_value}}</el-col>
                                      </el-row>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="10" class="queryTransactionModalDataBoxTitle">點數/虛擬幣：</el-col>
                                        <el-col :span="14" class="queryTransactionModalDataBoxData">點數 {{(queryTransactionXs.exchange_value)*(queryTransactionXs.exchange_rate)}}</el-col>
                                      </el-row>
                                      <el-row class="queryTransactionModalDataBox">
                                        <el-col :span="10" class="queryTransactionModalDataBoxTitle">兌換結果：</el-col>
                                        <el-col :span="14" class="queryTransactionModalDataBoxData">{{queryTransactionXs.status}}</el-col>
                                      </el-row>
                                    </slot>
                                  </el-col>
                                </el-row>
                              </el-col>
                          </div>
                        </div>
                      </transition>
                    </el-row>   
  </div>
</div>
</template>
<script>
import Client from "../js/Client.js";
import GameMgr from "../js/GameMgr.js";
import WebSocketNameTag from "../js/WebSocketNameTag.js";
import TransactionLayer from "../js/transaction.js";
export default {
  data() {
    var Today = new Date();
    // var Lang = GameMgr.globalLang;
    var Xs = GameMgr.getInstance().getXs();
    var t=document.body.clientWidth;
    var bodyHeight =Math.max( window.innerHeight, document.body.clientHeight );
    if(Xs){
      var boxHeight=bodyHeight-196;
    }else{
      var boxHeight=bodyHeight-340;//271//275//86
    }

    return {
      Xs: Xs,
      ethereum: "",
      boxHeight:boxHeight,
      TransactionChecked: false,
      showTransactionStep2: false,
      showTransactionClose: false,
      showQueryTransactionModal: false,
      startDate: Today,
      endDate: Today,
      startDateOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      endDateOptions: {
        disabledDate(time) {
          var a = time.getTime() > Date.now();
          // var b = new Date(end_date);
          // var c=b>time.getTime();
          return a;
        }
      },
      searchCategorySelected: "all", //wallets[0]["wallet_address"]//"輸入您註冊的錢包位置"
      searchCategoryOptions: [
        {
          id: "deposit",
          label: "入金"
        },
        {
          id: "withdraw",
          label: "出金"
        },
        {
          id: "all",
          label: "全部"
        }
      ],
      queryTransactionList: [],
      queryTransactionXs:'',
      language: this.$lang.cn,
      Lang000: "cn"
    };
  },
  watch: {},
  methods: {
    //**Language**//
    getLanguageData: function() {
      console.log("GLOBAL  ACC!!!!!!: " + GameMgr.globalLang);
      var LangA = GameMgr.globalLang;
      if (LangA == "en") {
        this.Lang000 = LangA;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (LangA == "cn") {
        this.Lang000 = LangA;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (LangA == "tw") {
        this.Lang000 = LangA;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      }
    },
    //**Language**//
    openShowQueryTransactionModal: function(queryTransactionXs) {
      this.queryTransactionXs=queryTransactionXs;
      this.showQueryTransactionModal=true;
    },
    queryTransaction: function() {
      var dateYMD = function(_ymd) {
        var date = new Date(_ymd);

        var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
        var M =
          (date.getMonth() + 1 < 10
            ? "0" + (date.getMonth() + 1)
            : date.getMonth() + 1) + "-";
        var Y = date.getFullYear() + "-";

        var ymd = Y + M + D;
        return ymd;
      };
      var start_date = dateYMD(this.startDate);
      var end_date = dateYMD(this.endDate);

      var transactionlayer = new TransactionLayer();
      transactionlayer.queryTransaction(
        this.searchCategorySelected,
        start_date,
        end_date
      );
    }
  },
  beforeCreate: function() {
    console.log("onmodifyLang beforeCreate!!!!!!: " + GameMgr.globalLang);
    var showMenuName = "home4";
    GameMgr.getInstance().setShowMenuName(showMenuName);
  },
  created: function() {
    window.myApp.$on(
      "onQueryTransaction",
      function() {
        //debugger
        this.queryTransactionList = [];
        var _queryTransactionList = GameMgr.getInstance().getQueryTransaction();
        for (var i = 0; i < _queryTransactionList.length; i++) {
          var queryTransaction = new Object();
          if (_queryTransactionList[i]["action_type"] == "deposit") {
            queryTransaction["action_type"] = "入金";
          }
          if (_queryTransactionList[i].action_type == "withdraw") {
            queryTransaction["action_type"] = "出金";
          }
          if (_queryTransactionList[i].action_type == "all") {
            queryTransaction["action_type"] = "全部";
          }
          if (_queryTransactionList[i].status == 0) {
            queryTransaction["status"] = "未完成";
          }
          if (_queryTransactionList[i].status == 1) {
            queryTransaction["status"] = "已完成";
          }
          queryTransaction["date"] = _queryTransactionList[i].date;
          queryTransaction["from_wallet"] =
            _queryTransactionList[i].from_wallet;
          queryTransaction["exchange_rate"] =
            _queryTransactionList[i].exchange_rate;
          queryTransaction["exchange_value"] =
            _queryTransactionList[i].exchange_value;
          queryTransaction["order_id"] = _queryTransactionList[i].order_id;

          this.queryTransactionList.push(queryTransaction);
        }
      }.bind(this)
    );
  },
  mounted: function() {
    console.log("onmodifyLang mounted!!!!!!: " + GameMgr.globalLang);
    this.getLanguageData();
  },
  updated: function() {
    console.log("onmodifyLang updated!!!!!!: " + GameMgr.globalLang);
  },
  destroyed: function() {
    //**Language**//
    window.myApp.$off("onmodifyLang");
    window.myApp.$off("onQueryTransaction");
  }
};
</script>

<style lang="scss">
.index3Page {
  height: 100%;
  overflow: auto;
  background: #292929;
  .chart-container {
    width: 100%;
    height: 100%;
    float: left;
    background: #292929;
    .col {
      padding: 0px 0px;
    }
    .mainLogo {
      padding-top: 28px;
      padding-bottom: 28px;
      text-align: center;
      .mainLogoTransactionText {
        // padding:10px 0px;
        color: #ffffff;
        font-size: 20px;
        text-align: center;
        height: 30px;
        .timeSelect {
          vertical-align: top;
          display: inline-block;
          width: 150px;
          height: 30px;
          padding-left: 5px;
          color: #5f5f5f;
          font-size: 14px;
          border-width: 0px;
        }

        .searchButton {
          cursor: pointer;
          margin: 0px 0px;
          margin-left: 10px;
          height: 30px;
          line-height: 40px;
          text-align: center;
          display: inline-block;
          background-color: #00aeff;
          width: 150px;
        }

        .timeSelectXs {
          vertical-align: top;
          margin-left: 10px;
          display: inline-block;
          width: 150px;
          height: 30px;
          padding-left: 5px;
          color: #5f5f5f;
          font-size: 14px;
          border-width: 0px;
        }
        .timeSelectXsBottom {
          vertical-align: top;
          // margin-left: 10px;
          margin-top: 10px;
          display: inline-block;
          width: 150px;
          height: 30px;
          padding-left: 5px;
          color: #5f5f5f;
          font-size: 14px;
          border-width: 0px;
        }
        .timeSelectMarginLeft {
          margin-left: 5px;
        }

        .el-input__inner {
          border-radius: 0px;
          border-width: 0px;
          height: 30px;
          line-height: 30px;
        }
        .el-input__icon {
          line-height: 30px;
        }

        .searchButtonXs {
          cursor: pointer;
          margin: 0px 0px;
          margin-left: 10px;
          margin-top: 10px;
          height: 30px;
          line-height: 40px;
          text-align: center;
          display: inline-block;
          background-color: #00aeff;
          width: 150px;
        }
      }
    }

    .mainTransaction {
      text-align: center;
      // padding: 10px;
      .mainDialogTransactionStep {
          font-size: 16px;
          text-align: center;
          background-color: #1e1e1e;
          color: #ffffff;
          height: 64px;
          line-height: 64px;
          // margin-bottom: 20px;
        }
      .mainDialogTransaction {//0524
        padding: 20px 0px;
        overflow: auto;
        /* padding: 40px 85px; */
        background-color: #ffffff;
        height: 600px;//600px
        
        .mainDialogTransactionStep2 {
          font-size: 16px;
          text-align: center;
          color: #363636;
          /* height: 64px;
        line-height: 64px; */
          padding: 10px 0px;
          .mainDialogTransactionStep2Top {
            padding-bottom: 5px;
            .queryTransactionImg {
              // width: auto;
              // height: auto;
              max-width: 100%;
              max-height: 100%;
            }
          }
        }
        .mainDialogTransactionStep2BG1 {
          background-color: #ffffff;
        }
        .mainDialogTransactionStep2BG2 {
          background-color: #eeeeee;
        }
      }
    }
  }
  /*****/
  .modal-mask-queryTransactionModal {
      position: fixed;
      z-index: 9999;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.45);
      display: table;
      transition: opacity 0.3s ease;

      .modal-wrapper {
        display: table-cell;
        vertical-align: middle;
        .modal-container {
          // width: 500px; //***
          margin: 0px auto;
          // padding: 18px 0px; //top/right
          background-color: rgba(0, 0, 0, 0);
          
          transition: all 0.3s ease;
          font-family: Helvetica, Arial, sans-serif;

          .el-icon-close{
            cursor: pointer;
            padding:10px 0px;
            color:#ffffff;
            font-size:20px;
            font-weight: bolder;
          }
          .modal-container-body {
            border-radius: 2px;
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
            text-align: center;
            background-color: #ffffff;
            // margin: 20px 0;
            padding: 30px 5px;
            .queryTransactionModalDataBox {
              color: #383838;
              font-size: 18px;
              padding:10px 0px;
              .queryTransactionModalDataBoxTitle{
                text-align: left;
              }
              .queryTransactionModalDataBoxData{
                text-align: right;
              }
              .queryTransactionModalDataBoxDataWallet{
                width:250px;
                // margin:0px 10px;
                padding-top:10px;
                text-align: left;
                font-size: 15px;
                word-wrap:break-word;
              }
            }
          }
        }
      }
    }
    .modal-enter {
      opacity: 0;
    }

    .modal-leave-active {
      opacity: 0;
    }

    .modal-enter .modal-container,
    .modal-leave-active .modal-container {
      -webkit-transform: scale(1.1);
      transform: scale(1.1);
    }
    /****/
}
</style>


