import GameMgr from './GameMgr.js';
export default class Client
{
    constructor()
    {
        this.listeners = [];
    }
    static getInstance()
    {
          if(this.instance == null)
             this.instance = new Client();
          return this.instance;
    }
	
    openConnection(uri)
    {
		// localStorage.setItem('uri', "ws://gs.didugogame.net:9003");
        // var uriStor = localStorage.getItem('uri');
        // this.ws = new WebSocket(uriStor);debugger
        this.ws = new WebSocket(uri);
        this.ws.onmessage = this.onMessage.bind(this);
        this.ws.onerror = this.onError.bind(this);
        this.ws.onopen = this.onOpen.bind(this);
        this.ws.onclose =  this.onClose.bind(this);
    }

    onMessage(message)
    {
            for(var i = 0 ; i <this.listeners.length; i++)
            {
                this.listeners[i][0](message.data);
            }
    }

    onOpen()
    {
        console.log('ws connected\n');
    }

    onClose(message)
    {
        window.myApp.$emit("onServerClose");
        window.console.log("onClose",message);
    }

    onError()
    {

    }

    sendMessage(msg)
    {
        this.ws.send(msg);
    }

    addListener(listener)
    {
       this.listeners.push(listener);
    }

    removeListener(observer)
    {
        for(var i = 0 ; i <this.listeners.length; i++)
        {
            if(this.listeners[i][1]==observer)
             this.listeners.splice(i,1);
        }
    }
	
	printSomething()
	{
		console.log("i am worked_loginLayer");
	}
}


