import Client from './Client.js';
import WebSocketNameTag from './WebSocketNameTag.js';

export default class GameMgr {
        
	constructor() {
        
        var _Xs;
        var _showMenuName;
        var _loginVerify;
        var _auth_token;
        var _username;
        var _password;
        var _exchangeRate;
        var _qrcodeUrl;
        var _contractAddres;
        var _wallets;
        var _points;
        var _queryTransaction;
        var _walletList;
        var _errorMessage;
        var _errorTitle;
        
        
		
        this.setXs = function (Xs) { _Xs = Xs; }
        this.getXs = function () { return _Xs; }
   
        this.setLoginVerify = function (loginVerify) { _loginVerify = loginVerify; sessionStorage["coinsloginVerify"] = loginVerify; 
        window.myApp.$emit("onLoginVerify"); }
        this.getLoginVerify = function () 
        { return sessionStorage["coinsloginVerify"];};

        this.setAuth_token = function (auth_token) { _auth_token = auth_token; sessionStorage.setItem('coinsauth_token', auth_token); }
        this.getAuth_token = function () { return sessionStorage.getItem('coinsauth_token'); }

        this.setUsername = function (username) { _username = username; sessionStorage["coinsusername"] = username; }
        this.getUsername = function () { return sessionStorage["coinsusername"]; }

        this.setPassword = function (password) { _password = password; sessionStorage["coinspassword"] = password; }
        this.getPassword = function () { return sessionStorage["coinspassword"]; }

        this.setWalletList = function (walletList) { _walletList = walletList;window.myApp.$emit("onWalletList"); }
        this.getWalletList = function () { return _walletList; }

        this.setExchangeRate = function (exchangeRate) { _exchangeRate = exchangeRate;}
        this.getExchangeRate = function () { return _exchangeRate;}

        this.setPoints = function (points) { _points = points;}
        this.getPoints = function () { return _points;}

        this.setQrcodeUrl = function (qrcodeUrl) { _qrcodeUrl = qrcodeUrl;}
        this.getQrcodeUrl = function () { return _qrcodeUrl; }

        this.setContractAddress = function (contractAddres) { _contractAddres = contractAddres;}
        this.getContractAddress = function () { return _contractAddres; }

        this.setWallets = function (wallets) { _wallets = wallets;}
        this.getWallets = function () { return _wallets; }
   
        this.setShowMenuName = function (showMenuName) { _showMenuName = showMenuName;window.myApp.$emit("onShowMenuName");}
        this.getShowMenuName = function () { return _showMenuName; }

        this.setQueryTransaction = function (queryTransaction) { _queryTransaction = queryTransaction;}
        this.getQueryTransaction = function () { return _queryTransaction; }

        this.setErrorMessage = function (errorMessage) { _errorMessage = errorMessage;}
        this.getErrorMessage = function () { return _errorMessage; }

        this.setErrorTitle = function (errorTitle) { _errorTitle = errorTitle;}
        this.getErrorTitle = function () { return _errorTitle; }

        

        //**lang**//
        var globalLang = "cn";
        var audioButton = 0;
        //**lang**//

        //**token**//
        var tok = "";
        var ver = ""
        var gameTok = "";
        //**token**//
		

    }

    static getInstance() {//
        if (this.instance == null)
            this.instance = new GameMgr();
        return this.instance;
    }
}


