import Vue from 'vue';
import GameMgr from './GameMgr.js';
import WebSocketNameTag from './WebSocketNameTag.js';
export default class LoginLayer {
  constructor() { }
  login(username, password) {
    // this.$http
    Vue.http.post("http://binary-bo.didugogame.net/api/coins/login", {
      username: username,
      password: password
    })
      .then(function (data) {//debugger
        if (data.ok) {
          var s = JSON.stringify(data);
          console.log("T: " + s);

          status = data["body"]["status_code"];
          if (status == "0") {
            GameMgr.getInstance().setAuth_token(
              data["body"]["data"]["auth_token"]
            );
            GameMgr.getInstance().setExchangeRate(
              data["body"]["data"]["exchange_rate"]
            );
            GameMgr.getInstance().setContractAddress(
              data["body"]["data"]["contract_address"]
            );
            GameMgr.getInstance().setQrcodeUrl(
              data["body"]["data"]["qrcode_url"]
            );
            GameMgr.getInstance().setWallets(
              data["body"]["data"]["wallets"]
            );
            GameMgr.getInstance().setPoints(
              data["body"]["data"]["points"]
            );
            var LoginVerify = true; //false//true
            GameMgr.getInstance().setLoginVerify(LoginVerify);
            GameMgr.getInstance().setUsername(username);
            GameMgr.getInstance().setPassword(password);

            window.myApp.$emit("onLogin");
          } else {
            var errorTitle="登入失敗";
            var errorMessage=data["body"]["message"];
            
            GameMgr.getInstance().setErrorTitle(errorTitle);
            GameMgr.getInstance().setErrorMessage(errorMessage);

            window.myApp.$emit("onError");
            console.log("**********login Error***********");
          }
        } else {
          console.log("ERR: " + error);
          this.logining = false;
        }
      });
  }

  register(username, password, nickname, mobile, email) {
    var registerObj = {
       username: username,
       password: password,
       nickname: nickname,
       mobile: mobile,
       email: email,
    };
    var walletList = GameMgr.getInstance().getWalletList();
    var walletI=0;
    for(var i=0;i<walletList.length;i++){
      if(walletList[i]["wallet"] !== undefined && walletList[i]["wallet"]!= ""){
        walletI=walletI+1;
        registerObj["wallet"+(walletI)]=walletList[i]["wallet"];
      }
    }
    Vue.http.post("http://binary-bo.didugogame.net/api/coins/register", 
      registerObj
    )
      .then(function (data) {
        
        if (data.ok) {
          var s = JSON.stringify(data);
          console.log("T: " + s);

          status = data["body"]["status_code"];
          if (status == "0") {
            var register = data; 
            window.myApp.$emit("onRegisterSuccess");
          } else {
            console.log("**********login Error***********");
            //debugger
            var errorTitle="註冊失敗";
            var errorMessage=data["body"]["message"];
            
            GameMgr.getInstance().setErrorTitle(errorTitle);
            GameMgr.getInstance().setErrorMessage(errorMessage);
            window.myApp.$emit("onRegisterError");
          }
        } else {
          console.log("ERR: " + error);
        }
      });
  }

  changePassword(old_password, new_password) {//debugger
    var auth_token = GameMgr.getInstance().getAuth_token();
    this.$http
    Vue.http.post("http://binary-bo.didugogame.net/api/coins/changePwd", {
      auth_token: auth_token,
      old_password: old_password,
      new_password: new_password
    })
      .then(function (data) {
        if (data.ok) {
          var s = JSON.stringify(data);
          console.log("T: " + s);

          status = data["body"]["status_code"];
          if (status == "0") {
            var changePassword = data;//debugger
          } else {
            console.log("**********forgotUsername Error***********");
          }
        } else {
          console.log("ERR: " + error);
          this.logining = false;
        }
      });
  }

  forgotUsername(email) {//debugger
    this.$http
    Vue.http.post("http://binary-bo.didugogame.net/api/coins/forgotUsername", {
      email: email
    })
      .then(function (data) {
        if (data.ok) {
          var s = JSON.stringify(data);
          console.log("T: " + s);

          status = data["body"]["status_code"];
          if (status == "0") {
            var forgotUsername = data;
            window.myApp.$emit("onRegisterSuccess");
          } else {
            console.log("**********forgotUsername Error***********");
            var errorTitle="請重新輸入一次";
            var errorMessage=data["body"]["message"];
            
            GameMgr.getInstance().setErrorTitle(errorTitle);
            GameMgr.getInstance().setErrorMessage(errorMessage);
            window.myApp.$emit("onRegisterError");
          }
        } else {
          console.log("ERR: " + error);
          this.logining = false;
        }
      });
  }

  forgotPassword(username, email) {
    this.$http
    Vue.http.post("http://binary-bo.didugogame.net/api/coins/forgotPwd", {
      username: username,
      email: email
    })
      .then(function (data) {
        if (data.ok) {
          var s = JSON.stringify(data);
          console.log("T: " + s);

          status = data["body"]["status_code"];
          if (status == "0") {
            var forgotPassword = data;
            window.myApp.$emit("onRegisterSuccess");
          } else {
            console.log("**********forgotPassword Error***********");
            var errorTitle="請重新輸入一次";
            var errorMessage=data["body"]["message"];
            
            GameMgr.getInstance().setErrorTitle(errorTitle);
            GameMgr.getInstance().setErrorMessage(errorMessage);
            window.myApp.$emit("onRegisterError");
          }
        } else {
          console.log("ERR: " + error);
          this.logining = false;
        }
      });
  }

  handleLogin() {
    console.log("login");
  }

  show() {
    var lobbylayer = new LobbyLayer(this);
    lobbylayer.lobby();
  }
}
