
<template>
	<el-row class="container">
	<!--header-->
		<el-col :span="24" class="header" v-bind:class="{'headerBgXs':Xs==true,'headerBgSm':Xs==false}">
      <el-row>
          <el-col :span="2" :offset="2" :sm="{span: 4, offset: 4}" :md="{span: 4, offset: 4}">
            <el-row class="headerLogo"  type="flex" align="middle">
              <img src="../../static/img/ethereumWebImg/logo_01.png" class="headerLogoIn" v-if="Xs == false"><!--********d-none d-sm-block*******-->
              <img src="../../static/img/ethereumWebImg/Option01.png" class="headerLogoIn" v-if="Xs == true&&LoginVerify==true" v-on:click="OpenMenuXs">
            </el-row>
          </el-col>
          
          <el-col :span="8" :offset="12" :sm="{span: 10, offset: 2}" :md="{span: 10, offset: 2}"  class="headerTextOut" v-if="LoginVerify==false">
            <el-row class="headerText" type="flex" align="middle">
              <el-col :span="6" v-if="Xs == false"><!--********d-none d-sm-block*******-->
                <el-row class="headerLogoInInputMain">
                  <el-col :span="20" class="headerLogoInInputMain">
                    <el-row><input class="headerLogoInInputMainIn" placeholder="帳號" v-model="userName"></el-row>
                  </el-col>
                  <el-col :span="4"  class="headerLogoInCheckboxOut">
                    <el-row><input id="checkBox" type="checkbox" class="headerLogoInCheckbox" v-model="userNameChecked"></el-row>
                  </el-col>
                </el-row>
                <el-col :span="20" class="headerLogoInInputMainText headerTextPointerIcons" v-on:click.native="forgotUsernameLink">
                  忘記帳號
                </el-col>
              </el-col>

              <el-col :span="6" v-if="Xs == false"><!--********d-none d-sm-block*******-->
                <el-row class="headerLogoInInputMain">
                  <el-col :span="20" class="headerLogoInInputMain">
                    <el-row><input class="headerLogoInInputMainIn" placeholder="密碼" type="password" v-model="userPassword"></el-row>
                  </el-col>
                  <el-col :span="4" class="headerLogoInCheckboxOut">
                    <el-row><input id="checkBox" type="checkbox" class="headerLogoInCheckbox" v-model="userPasswordChecked"></el-row>
                  </el-col>
                </el-row>
                <el-col :span="20"  class="headerLogoInInputMainText headerTextPointerIcons" v-on:click.native="forgotPasswordLink">
