                </el-col>
              </el-col>
              <el-col :span="8" class="headerLogoInButton" v-if="Xs == false"><!--********d-none d-sm-block*******-->
                <el-row class="headerContainerButton">
                  <el-col :span="10" :offset="2" class="headerContainerButtonConfirm" v-on:click.native="login">
                      <div class="headerContainerText">登入</div>
                  </el-col>
                  <el-col :span="10" :offset="2" class="headerContainerButtonCancel" v-on:click.native="registerLink">
                      <div class="headerContainerText">註冊</div>
                  </el-col>
                </el-row>
              </el-col>

              <el-col :span="24" :offset="0" :sm="{span: 4, offset: 0}" v-bind:class="{'headerLogoInButton':Xs==false}">
              <!-- <el-col :span="24" class="headerLogoInButton" v-if="Xs != false"> -->
                <el-row class="languageMenu">
                    <el-dropdown trigger="click">
                      <el-row class="el-dropdown-link headerLogoInLanguageOut"  type="flex" align="middle">
                       <img src="../../static/img/ethereumWebImg/i01.png" class="headerLogoInLanguage" />&nbsp;&nbsp; 繁體中文
                      </el-row>
                      <el-dropdown-menu slot="dropdown" class="languageDropdownMenu"  v-on:click="openLanguageDropdownMenu">
                        <el-dropdown-item>English</el-dropdown-item>
                        <el-dropdown-item>中文(繁體)</el-dropdown-item>
                        <el-dropdown-item>中文(簡體)</el-dropdown-item>
                      </el-dropdown-menu>
                    </el-dropdown>
                </el-row>
              <!-- </el-col> -->
              </el-col>
            </el-row>
          </el-col>


          <el-col :span="20" :offset="0" :sm="{span: 10, offset: 2}" class="headerTextOut"  v-if="LoginVerify ==true">
            <el-row class="headerText" type="flex" align="middle" >
              <el-col :span="8" :offset="6" :sm="{span: 4, offset: 8}" class="headerLoginTextA">
                {{loginUserName}}
              </el-col>
              
              <el-col :span="6" :offset="0" :sm="{span: 4, offset: 0}" class="headerLoginTextA">
                <el-row class="headerLogoInMImgOut"  type="flex" align="middle" justify="center">
                  <img src="../../static/img/ethereumWebImg/i06.png" class="headerLogoInMImg" />
                  {{points}}
                </el-row>
              </el-col>

              <!-- <el-col :span="2" :offset="0" v-on:click.native="showMoneyOut=true">
                轉出
              </el-col>

              <el-col :span="2" :offset="0">
                轉入
              </el-col> -->

              <el-col :span="3" :offset="0" class="headerLogoInSignOut" v-if="Xs == false" v-on:click.native="signOut"><!--d-none d-sm-block-->
                <u>登出</u>
              </el-col>

              <el-col :span="4" :offset="1" v-if="Xs == false"><!--d-none d-sm-block-->
                <el-row class="languageMenu">
                   <el-dropdown trigger="click">
                      <el-row class="el-dropdown-link headerLogoInLanguageOut"  type="flex" align="middle">
                       <img src="../../static/img/ethereumWebImg/i01.png" class="headerLogoInLanguage" />&nbsp;&nbsp; 繁體中文
                      </el-row>
                      <el-dropdown-menu slot="dropdown" class="languageDropdownMenu">
                        <el-dropdown-item>English</el-dropdown-item>
                        <el-dropdown-item>中文(繁體)</el-dropdown-item>
                        <el-dropdown-item>中文(簡體)</el-dropdown-item>
                      </el-dropdown-menu>
                    </el-dropdown>
                </el-row >
              </el-col>
            </el-row>
          </el-col>
      </el-row>
 <!--********Header*******-->

      <!---->
      <el-row>
                      <transition name="modal">
                        <div class="modal-mask-errorModal" v-show="showErrorModal">
                          <div class="modal-wrapper">
                              <el-col :span="22" :offset="1" :sm="{span: 6, offset: 9}">
                                <el-row class="modal-container">
                                  <el-col :span="24">
                                     <el-row type="flex"  align="middle" justify="end">
                                    <i class="el-icon-close" v-on:click="showErrorModal=false"></i>
                                     </el-row>
                                  </el-col>
                                  <el-col :span="24" class="modal-container-body">
                                    <slot>
                                      <el-col :span="24" class="modal-container-body-title">{{errorTitle}}</el-col>
                                      <el-col :span="24" class="modal-container-body-text">{{errorMessage}}</el-col>
                                    </slot>
                                  </el-col>
                                </el-row>
                              </el-col>
                          </div>
                        </div>
                      </transition>
      </el-row> 

      <el-row>
                      <transition name="modal">
                        <div class="modal-mask-errorModal" v-show="showServerCloseModal">
                          <div class="modal-wrapper">
                              <el-col :span="22" :offset="1" :sm="{span: 6, offset: 9}">
                                <el-row class="modal-container">
                                  <el-col :span="24" class="modal-container-body">
                                    <slot>
                                      <el-col :span="24" class="modal-container-body-title">伺服器斷線</el-col>
                                      <el-col :span="24" class="modal-container-body-text">請重新登入，謝謝</el-col>
                                      <el-button type="primary" class="modal-container-body-button" v-on:click.native="signOut">確定</el-button>
                                    </slot>
                                  </el-col>
                                </el-row>
                              </el-col>
                          </div>
                        </div>
                      </transition>
      </el-row> 
      <!---->
      <el-row>
        <transition name="modal">
          <div class="modal-mask-MoneyOut" v-show="showMoneyOut">
            <div class="modal-wrapper">
                <el-col :span="22" :offset="1" :sm="{span: 6, offset: 9}">
                  <el-row class="modal-container">
                    <el-col :span="24">
                                     <el-row type="flex"  align="middle" justify="end">
                                    <i class="el-icon-close" v-on:click="showQueryTransactionModal=false"></i>
                                     </el-row>
                   </el-col>
                    <el-col :span="24" class="modal-container-body">
                      <slot>
                      <el-col :span="24">目前持有總遊戲幣(大錢包)</el-col>
                      <el-col :span="24">{{points}}</el-col>
                      <el-col :span="24">
                        <el-row>
                          <el-col :span="12">MT4目前持有</el-col>
                          <el-col :span="12">0</el-col>
                        </el-row>
                        <el-col :span="24">
                        <input class="headerLogoInInputMainIn" placeholder="請輸入要轉出多少" >
                      </el-col>
                      </el-col>
                      <el-col :span="24">
                        <el-row>
                          <el-col :span="12">二元期權目前持有</el-col>
                          <el-col :span="12">0</el-col>
                        </el-row>
                        <el-col :span="24">
                        <input class="headerLogoInInputMainIn" placeholder="請輸入要轉出多少" >
                      </el-col>
                      </el-col>
                      <el-col :span="24"><el-button type="primary"  v-on:click.native="showMoneyOut=false">確定</el-button></el-col>
                      </slot>
                    </el-col>
                  </el-row>
                </el-col>
            </div>
          </div>
        </transition>
      </el-row>    		
		
      
      <!---->
      <el-row>
        <transition name="modal">
          <div class="modal-mask-menuXs" v-show="showMenuXs == true">
            <div class="modal-wrapper">
                <el-col :span="12" :offset="0" class="modal-container">
                    <el-col :span="24" class="modal-container-body">
                      <slot>
                          <el-col :span="18" :offset="3" class="modal-container-body-text" v-bind:class="{'menuNameon':showMenuName=='home1'}" v-on:click.native="indexLink">首頁</el-col>
                          <el-col :span="18" :offset="3" class="modal-container-body-text" v-bind:class="{'menuNameon':showMenuName=='home2'}" v-on:click.native="transactionALink">出金</el-col>
                          <el-col :span="18" :offset="3" class="modal-container-body-text" v-bind:class="{'menuNameon':showMenuName=='home3'}" v-on:click.native="transactionLink">入金</el-col>
                          <el-col :span="18" :offset="3" class="modal-container-body-text" v-bind:class="{'menuNameon':showMenuName=='home4'}" v-on:click.native="transaction3Link">兌換紀錄</el-col>
                          <el-col :span="18" :offset="3">
                            <el-row class="languageMenu">
                              <el-dropdown trigger="click">
                                  <el-row class="el-dropdown-link headerLogoInLanguageOut"  type="flex" align="middle">
                                  繁體中文
                                  </el-row>
                                  <el-dropdown-menu slot="dropdown" class="languageDropdownMenuXs">
                                    <el-dropdown-item>English</el-dropdown-item>
                                    <el-dropdown-item>中文(繁體)</el-dropdown-item>
                                    <el-dropdown-item>中文(簡體)</el-dropdown-item>
                                  </el-dropdown-menu>
                                </el-dropdown>
                            </el-row >
                        </el-col>
                        <el-col :span="9" :offset="2" class="modal-container-body-text" style="position: absolute;bottom:20px;" v-on:click.native="signOut">登出</el-col>
                      </slot>
                    </el-col>
                </el-col>
                <el-col :span="12" class="modal-container2" v-on:click.native="OpenMenuXs" ></el-col>
            </div>
          </div>
        </transition>
      </el-row>
      <!---->
      </el-col>
    <!--header-->
    <!--headerMenu-->
    <el-col :span="24" class="headerMenu" v-if="Xs == false">
      <el-row>
        <el-col :span="6" :offset="14" class="headerMenuText headerTextPointerIcons">
          <!-- <el-col>
           <template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
             <el-col :span="6" :key="item.id" v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"></el-col>
           </template>
          </el-col> -->
          <el-col :span="6" v-on:click.native="indexLink" v-bind:class="{'menuNameon':showMenuName=='home1'}">首頁</el-col>
          <el-col :span="6" v-on:click.native="transactionALink" v-bind:class="{'menuNameon':showMenuName=='home2'}">出金</el-col>
          <el-col :span="6" v-on:click.native="transactionLink" v-bind:class="{'menuNameon':showMenuName=='home3'}">入金</el-col>
          <el-col :span="6" v-on:click.native="transaction3Link" v-bind:class="{'menuNameon':showMenuName=='home4'}">兌換紀錄</el-col>
        </el-col>
      </el-row>
    </el-col>
    <!--headerMenu-->	
	
	<!--main-->
    <el-col :span="24" class="main"  v-bind:class="{'mainXs':Xs==true,'mainSm':Xs==false}">
      <!-- <el-col :xs="menuAsideXs":sm="1" id="menu" class="menuAside" >
              <el-menu :default-active="$route.path" router>
                <el-col>
               <template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
                  <el-menu-item :key="item.id" v-if="item.leaf&&item.children.length>0" :index="item.children[0].path" style="padding-left: 0px;">
                  </el-menu-item>
                </template>
                </el-col>
                  <el-col class="SignOut">
                  <el-button type="text" v-on:click="SignOut"><img src="/static/img/icon09.png" class="footerImg"></br><span>{{lang.Route_button_option9}}</span></el-button>
                </el-col>
              </el-menu>
      </el-col> -->

      <el-col id="main"  :span="24">
        <el-main class="content-container">
          <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in" >
              <router-view></router-view>
            </transition>
          </el-col>
        </el-main>
      </el-col>


    </el-col>
    <!--main-->
	</el-row>
</template>

<script>
import Client from "../js/Client.js";
import GameMgr from "../js/GameMgr.js"; //
import WebSocketNameTag from "../js/WebSocketNameTag.js";
import LoginLayer from "../js/Login.js";
//import app from '../main.js';

export default {
  data() {
    if (document.body.clientWidth < 768) {
      var Xs = true;
      GameMgr.getInstance().setXs(Xs);
    } else {
      var Xs = false;
      GameMgr.getInstance().setXs(Xs);
    }
    var LoginVerify = false; //false//true
    GameMgr.getInstance().setLoginVerify(LoginVerify);

    return {
      showMenuName: "home1",
      LoginVerify: LoginVerify,
      languageDropdownMenuTop: "top:80px;",
      showMenuXs: false,
      Xs: Xs,
      connectionAliveInterval: "",
      userName: "", //aaa111
      userPassword: "", //DEi8i5XR
      loginUserName: "",
      points: "",
      errorTitle: "",
      errorMessage: "",

      userNameChecked: false,
      userPasswordChecked: false,
      showServerCloseModal: false,
      showErrorModal: false,
      showMoneyOut: false,
      showMoneyIn: false,

      //***Head***//
      options: [
        /*{
          id: "tw",
          label: "繁體中文"
        },*/
        {
          id: "cn",
          label: "简体中文"
        },
        {
          id: "en",
          label: "English"
        }
      ],
      value1: "简体中文",
      //***Head***//

      //**Language**//
      lang: this.$lang.cn
      //**Language**//
    };
  },

  watch: {
    value1: {
      handler: function() {
        console.log(
          "== value1!!!!!!!!!!!!!!!!! ==" +
            this.value1.id +
            ", Lang:" +
            this.lang
        );
        if (this.value1.id == "en") {
          this.lang = this.$lang.en;
          GameMgr.globalLang = this.value1.id;
          console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
          window.myApp.$emit("onmodifyLang");
        } else if (this.value1.id == "cn") {
          this.lang = this.$lang.cn;
          GameMgr.globalLang = this.value1.id;
          console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
          window.myApp.$emit("onmodifyLang");
        } else if (this.value1.id == "tw") {
          this.lang = this.$lang.tw;
          GameMgr.globalLang = this.value1.id;
          console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
          window.myApp.$emit("onmodifyLang");
        }
      }
    }
  },
  //**Header**//

  methods: {
    registerLink: function() {
      this.$router.push({ path: "/register" });
      this.showMenuXs = false;
    },
    forgotPasswordLink: function() {
      this.$router.push({ path: "/forgotPassword" });
      this.showMenuXs = false;
    },
    forgotUsernameLink: function() {
      this.$router.push({ path: "/forgotUsername" });
      this.showMenuXs = false;
    },
    indexLink: function() {
      this.$router.push({ path: "/" });
      this.showMenuXs = false;
    },
    transactionALink: function() {
      if (this.LoginVerify == true) {
        this.$router.push({ path: "/withdraw" });
        this.showMenuXs = false;
      }
    },
    transactionLink: function() {
      if (this.LoginVerify == true) {
        this.$router.push({ path: "/deposit" });
        this.showMenuXs = false;
      }
    },
    transaction3Link: function() {
      if (this.LoginVerify == true) {
        this.$router.push({ path: "/transactionHistory" });
        this.showMenuXs = false;
      }
    },
    login: function() {
      var loginlayer = new LoginLayer();
      loginlayer.login(this.userName, this.userPassword);
    },
    openLanguageDropdownMenu: function() {},
    menuClick: function(menuName) {
      console.log(menuName); //sdebugger
      if (this.betRunningXs == 24) {
        this.betRunningXs = 0;
        this.mainXs = 24;
      }
      this.showMenuName = menuName;
    },
    OpenMenuXs: function() {
      if (this.showMenuXs == false) {
        //debugger
        this.showMenuXs = true;
      } else {
        this.showMenuXs = false;
      }
    },

    getLanguageData: function() {
      //**Language**//
      var Lang = GameMgr.globalLang;
      if (Lang == "en") {
        this.value1 = "English";
        this.lang = this.$lang.en;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (Lang == "cn") {
        this.value1 = "簡體中文";
        this.lang = this.$lang.cn;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (Lang == "tw") {
        this.value1 = "繁體中文";
        this.lang = this.$lang.tw;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      }
      //**Language**//
    },

    signOut: function() {
      var LoginVerify = false; //false//true
      GameMgr.getInstance().setLoginVerify(LoginVerify);
      this.LoginVerify = LoginVerify;
      this.showMenuXs = false;
      this.showServerCloseModal = false;
      //0524
      if (this.userNameChecked == true) {
        this.userName = localStorage.getItem('coinsLoginUsername');
      } else {
        this.userName = "";
      }
      if (this.userPasswordChecked == true) {
        this.userPassword = localStorage.getItem('coinsLoginPassword');
      }else{
        this.userPassword = "";
      }

    
      this.userNameChecked=false;
      this.userPasswordChecked=false;
      
      window.myApp.$emit("onsignOut");
      this.$router.push({ path: "/" });
    }
  },
  beforeCreate: function() {
    console.log("== HomeBeforeCreate ==");
    if (document.body.clientWidth < 768) {
      var Xs = true;
      GameMgr.getInstance().setXs(Xs);
    } else {
      var Xs = false;
      GameMgr.getInstance().setXs(Xs);
    }
    // var HomeVerify = GameMgr.getInstance().getHomeVerify();

    // if (HomeVerify == "Lobby") {
    //   GameMgr.getInstance().setHomeVerify("Home");
    //   console.log("LobbyLobbyLobbyLobbyLobby");
    // } else {
    //   var refresh = function() {
    //     console.log("HomeHomeHomeHomeHome");
    //     this.$router.push({ path: "/login" });
    //   }.bind(this);
    //   refresh();
    // }
  },
  created: function() {
    //0521
    console.log("== HomeCreated ==");
    //debugger
    if (this.LoginVerify == false) {
      this.$router.push({ path: "/index" });
    }
    window.myApp.$on(
      "onServerClose",
      function() {
        this.showServerCloseModal = true;
      }.bind(this)
    );
    window.myApp.$on(
      "onError",
      function() {
        var errorTitle = GameMgr.getInstance().getErrorTitle();
        var errorMessage = GameMgr.getInstance().getErrorMessage();
        this.errorTitle = errorTitle;
        this.errorMessage = errorMessage;
        this.showErrorModal = true;
      }.bind(this)
    );
    window.myApp.$on(
      "onLoginVerify",
      function() {
        //debugger
        var LoginVerify = GameMgr.getInstance().getLoginVerify();
        LoginVerify = JSON.parse(LoginVerify);
        this.LoginVerify = LoginVerify;
      }.bind(this)
    );
    window.myApp.$on(
      "onLogin",
      function() {
        if(this.Xs==false){
        if (this.userNameChecked == true) {
        localStorage.setItem("coinsLoginUsername", this.userName);
      } else {
        localStorage.removeItem("coinsLoginUsername");
      }
      if (this.userPasswordChecked == true) {
        localStorage.setItem("coinsLoginPassword", this.userPassword);
      } else {
        localStorage.removeItem("coinsLoginPassword");
      }
        }
        var loginUserName = GameMgr.getInstance().getUsername();
        var points = GameMgr.getInstance().getPoints();
        this.loginUserName = loginUserName;
        this.points = points;
        var LoginVerify = GameMgr.getInstance().getLoginVerify();
        LoginVerify = JSON.parse(LoginVerify);
        this.LoginVerify = LoginVerify;
      }.bind(this)
    );
    window.myApp.$on(
      "onShowMenuName",
      function() {
        //debugger
        var showMenuName = GameMgr.getInstance().getShowMenuName();
        this.showMenuName = showMenuName;
      }.bind(this)
    );
  },
  mounted: function() {
    console.log("== mounted ==");
    this.userName = localStorage.getItem('coinsLoginUsername');
    this.userPassword = localStorage.getItem('coinsLoginPassword');
  },
  destroyed: function() {
    //window.myApp.$off();
    console.log("== HomeDestroyed ==");
    // this.lobbyLayer.destory();
    // var destoryUpdataPrice = function() {
    //   window.myApp.$emit("destoryUpdataPrice");
    // };
    clearInterval(this.connectionAliveInterval);
    // destoryUpdataPrice();
    window.myApp.$off("onLoginVerify");
    window.myApp.$off("onShowMenuName");
    window.myApp.$off("onError");
  }
};
</script>

<style scoped lang="scss">
@import "~scss_vars";
.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  .el-col {
    padding: 0px 0px;
  }
  .el-row {
    padding: 0px 0px;
  }
  .headerMenu {
    //0510
    display: fixed;
    top: 80px;
    height: 45px;
    line-height: 45px;
    background-color: #1e1e1e;
    color: #fff;
