<script>
import Client from "../js/Client.js";
import GameMgr from "../js/GameMgr.js";
import WebSocketNameTag from "../js/WebSocketNameTag.js";
import TransactionLayer from "../js/transaction.js";
export default {
  data() {
    // var Lang = GameMgr.globalLang;
    var Xs = GameMgr.getInstance().getXs();
    var exchangeRate = GameMgr.getInstance().getExchangeRate();
    var wallets =  GameMgr.getInstance().getWallets();//debugger
    return {
      Xs: Xs,
      gameCurrency: 0,
      exchangeRate:exchangeRate,
      walletsSelected:wallets[0]["wallet_address"],//wallets[0]["wallet_address"]//"輸入您註冊的錢包位置"
      walletsOptions:wallets,
      TransactionChecked: false,
      showTransactionStep2: false,
      showTransactionClose: false,
      language: this.$lang.cn,
      Lang000: "cn"
    };
  },
  watch: {},
  methods: {
    //**Language**//
    getLanguageData: function() {
      console.log("GLOBAL  ACC!!!!!!: " + GameMgr.globalLang);
      var LangA = GameMgr.globalLang;
      if (LangA == "en") {
        this.Lang000 = LangA;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (LangA == "cn") {
        this.Lang000 = LangA;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      } else if (LangA == "tw") {
        this.Lang000 = LangA;
        console.log("GLOBAL!!!!!!: " + GameMgr.globalLang);
      }
    },
    //**Language**//
    transactionOk: function() {
      if (this.TransactionChecked == true) {
        this.showTransactionStep2 = true;
      }
    },
    withdraw: function() {
      var transactionlayer = new TransactionLayer();
      var gameCurrency=Number(this.gameCurrency);
      transactionlayer.withdraw(this.walletsSelected,gameCurrency);
      // this.showTransactionClose = true;
    }
  },
  beforeCreate: function() {
    console.log("onmodifyLang beforeCreate!!!!!!: " + GameMgr.globalLang);
    var showMenuName = "home2";
    GameMgr.getInstance().setShowMenuName(showMenuName);
  },
  created: function() {
   window.myApp.$on(
      "onSuccess",
      function() {
        this.showTransactionClose = true;
      }.bind(this)
    );
  },
  mounted: function() {
    console.log("onmodifyLang mounted!!!!!!: " + GameMgr.globalLang);
    this.getLanguageData();
  },
  updated: function() {
    console.log("onmodifyLang updated!!!!!!: " + GameMgr.globalLang);
  },
  destroyed: function() {
    //**Language**//
    window.myApp.$off("onmodifyLang");
    window.myApp.$off("onSuccess");
  }
};
</script>

