import Vue from 'vue';
import GameMgr from './GameMgr.js';
import WebSocketNameTag from './WebSocketNameTag.js';
export default class TransactionLayer
{
    constructor(){}
    deposit(wallet,ethValue){

        var auth_token = GameMgr.getInstance().getAuth_token();
        this.$http
        Vue.http.post("http://binary-bo.didugogame.net/api/coins/deposit", {
        auth_token: auth_token,
        wallet: wallet,
        ethValue:ethValue
        })
        .then(function(data) {
          if (data.ok) {
            var s = JSON.stringify(data);
            console.log("T: " + s);
          
            status = data["body"]["status_code"];
            if (status == "0") {
              window.myApp.$emit("onSuccess");
            }else if(status == "102"){
              window.myApp.$emit("onServerClose");
            }  else {
            var errorTitle="兌換失敗";
            var errorMessage=data["body"]["message"];
            
            GameMgr.getInstance().setErrorTitle(errorTitle);
            GameMgr.getInstance().setErrorMessage(errorMessage);

            window.myApp.$emit("onDepositError");
            window.myApp.$emit("onError");
              console.log("**********deposit Error***********");
            }
          } else {
            window.myApp.$emit("onServerClose");
            console.log("ERR: " + error);
          }
        });
    }

    withdraw(wallet,pointValue){
      var auth_token = GameMgr.getInstance().getAuth_token();
      this.$http
      Vue.http.post("http://binary-bo.didugogame.net/api/coins/withdraw", {
      auth_token: auth_token,
      wallet: wallet,
      pointValue:pointValue
      })
      .then(function(data) {
        if (data.ok) {
          var s = JSON.stringify(data);
          console.log("T: " + s);
        
          status = data["body"]["status_code"];
          if (status == "0") {
            //debugger
            window.myApp.$emit("onSuccess");
          }else if(status == "102"){
            window.myApp.$emit("onServerClose");
          } else {
            var errorTitle="兌換失敗";
            var errorMessage=data["body"]["message"];
            
            GameMgr.getInstance().setErrorTitle(errorTitle);
            GameMgr.getInstance().setErrorMessage(errorMessage);
            
            window.myApp.$emit("onError");
            console.log("**********deposit Error***********");
          }
        } else {
          window.myApp.$emit("onServerClose");
          console.log("ERR: " + error);
          this.logining = false;
        }
      });
  }

  queryTransaction(action_type,startDate,endDate){
    var auth_token = GameMgr.getInstance().getAuth_token();
    this.$http
    Vue.http.post("http://binary-bo.didugogame.net/api/coins/queryTransaction", {
    auth_token: auth_token,
    action_type: action_type,
    startDate:startDate,
    endDate:endDate
    })
    .then(function(data) {
      if (data.ok) {
        var s = JSON.stringify(data);
        console.log("T: " + s);
      
        status = data["body"]["status_code"];
        if (status == "0") {
            var queryTransaction=data["body"]["data"]["records"];
            GameMgr.getInstance().setQueryTransaction(queryTransaction);
            window.myApp.$emit("onQueryTransaction");
        }else if(status == "102"){
          window.myApp.$emit("onServerClose");
        } else {
          var errorTitle="查詢失敗";
          var errorMessage=data["body"]["message"];
          
          GameMgr.getInstance().setErrorTitle(errorTitle);
          GameMgr.getInstance().setErrorMessage(errorMessage);

          window.myApp.$emit("onError");
          console.log("**********deposit Error***********");
        }
      } else {
        window.myApp.$emit("onServerClose");
        console.log("ERR: " + error);
        this.logining = false;
      }
    });
}

}


